.topbar {
  width: 100%;
  margin-bottom: 30px;
}

.topbarWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.greeting {
  font-size: 20px;
  font-weight: 500;
  color: black;
}

.greetIcon {
  font-size: 22x;
  color: #c68642;
}

.searchbar {
  border: none;
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 4px;
}

.searchIcon {
  font-size: 24px;
  margin: 0 5px;
  color: coral;
}

.searchImgContainer {
  padding: 5px;
  border: solid;
  border-radius: 50%;
}

.searchInput {
  border: none;
  border-radius: 0 4px 4px 0;
  outline: none;
  padding: 0;
  margin: 0;
}
